// @use './datepicker';

$bg-gray: #eee;

@mixin grid-display {
  display: grid;
  text-align: center;
  grid-template-columns: repeat(7, 1fr);
}

.miq-weekdays {
  @include grid-display;
}

.miq-week {
  padding-top: 4px;
  padding-bottom: 4px;
}

.miq-week-dates {
  @include grid-display;

  .miq-day {
    padding: 2px;
  }
}

.miq-calendar {
  .icon-btn {
    cursor: pointer;
    width: 32px;
    height: 32px;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $bg-gray;

    svg {
      height: 18px;
      width: 18px;
    }
  }
}

.miq-datepicker-day {
  .miq-datepicker-btn {
    border-color: transparent;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: var(--gray-200);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

    &:hover {
      color: var(--blue-700);
      background-color: var(--blue-100);
      border-color: var(--blue-700);
    }

    &.today {
      color: rgb(25, 103, 210);
      font-weight: 500;
      background-color: var(--blue-100);
    }

    &.active {
      color: white;
      font-weight: 500;
      background-color: var(--blue-600);
    }
  }
}
